import React, { useState } from "react";
import HumanMessage from "./HumanMessage";
// import BotMessage from "./BotMessage";
import botpp from "../images/logo.png";
import ScrollableFeed from "react-scrollable-feed";

// --------------------------------Chatbox is inspired from the Todo-List in displaying the messages..

const ChatBox = () => {
  // state for the input text by the user
  const [text, setText] = useState("");

  // array of text input by the user
  const [msges, setMsges] = useState([]);

  // --------------after clicking the sent button user inputs are saved in array(msges)
  const onClick = (e) => {
    // --------to submit via Enter button, we use 'onSubmit' and to prevent from refreshing page automatically on submisson,we use preventdefault
    e.preventDefault();
    if (!text) {
    } else {
      // ---------------------------------TO ADD THE USER INPUT IN ARRAY ,WE USE SPREAD OPERATOR----->(IMPORTANT!!!!!!!)
      setMsges([...msges, text]);
      setText("");
    }
  };

  return (
    <>
      <div className="center">
        <div className="container">
          <div className="center-h">
            <div className="header">
              <div className="bot-profile">
                <img src={botpp} alt="logo"></img>
              </div>
              <div className="Title">
                <p1>Hello There !</p1>
                <p>Active Now</p>
              </div>
              <div className="icons"></div>
              <i class="fas fa-phone-alt"></i>
              <i class="fas fa-video"></i>
              <i class="fas fa-info-circle"></i>
            </div>
          </div>
          <ScrollableFeed>
            <div className="center-mb">
              <div className="msg-body">
                {/*------------------------------------- displaying the list of user inputs */}
                {msges.map((msg, idx) => {
                  return <HumanMessage text={msg} key={idx} />;
                })}
              </div>
            </div>
          </ScrollableFeed>
          <div className="userinput">
            <div className="box">
              <form onSubmit={onClick}>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Type here"
                  value={text}
                  onChange={(e) => setText(e.target.value)}
                  autoFocus
                />
              </form>
            </div>
            <div className="sent">
              <i class="far fa-paper-plane" onClick={onClick}></i>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ChatBox;
